import { Label } from "reactstrap";
import { Instagram, React } from "../toolbox/CustomSvgs";
import { MultipleRows } from "../toolbox/SlickSlider";
import { useEffect, useId, useLayoutEffect, useRef } from "react";
import SliderComponent from "../toolbox/SliderComponent";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";


export default function SkillsPage({ className, ...props }) {


  let skills = [
    {
      title: "JavaScript",
      imgUrl:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSqYfbaJ3eCkft5cB1BjSSN8pwLqODTGLTqOXQyIUhvWQSVSPgyG6bI5iH4S3mCVPdIV7w&usqp=CAU",
      imgComponent: null,
      backgroundColor: "yellow",
      color: "black",
    },
    {
      title: "React.js",
      imgUrl: "",
      imgComponent: <React width="60%" height="100%" />,

      color: "#000",
    },
    {
      title: "CSS",
      imgUrl:
        "https://play-lh.googleusercontent.com/ajbmMCoaThQcD4_z-1-6H79M0ItJ1Vz2jW_5yRB_eb1d_Fdzmdci0SPHFfujQj23n-Q",
      imgComponent: null,
      backgroundColor: "",
      color: "black",
    },
    {
      title: "Android Developper",
      imgUrl:
        "data:image/png;base64,",
      imgComponent: null,
      backgroundColor: "green",
      color: "black",
    },
  ];

  return (
    <div
      className={className}
      id="SkillsPage"
      style={{
        height: "90vh",
        background: "linear-gradient(180deg,#400052,#270053)",
        placeItems: "center",
        display: "grid",
      }}
    >
      <div
        className="  border"
        style={{
          width: "95vw",
          height: "80vh",
          backgroundColor: "rgba(169,169,169,0.3)",
          backgroundBlendMode:"difference",
          borderRadius: "50px",
          overflow: "hidden",
          display: "flex",
        }}
      >
        <div className="  " style={{ margin: "auto", width: "100%" }}>
          <SliderComponent
            slidesToShow={2}
            
            slides={skills.map((item) => (
              <div
                style={{
                  minHeight: "200px",
                  backgroundColor: item.backgroundColor || "rgba(0,0,0,0.5)",
                  border: "2px solid #707070",
                  borderRadius: "50px",
                  display: "grid",
                  placeItems: "center",
                  padding: "5px",
                  margin: "15px",
                  overflow: "hidden",
                }}
                key={1}
              >
                <div
                  style={{
                    width: "100%",
                    display: "grid",
                    placeItems: "center",
                    height: "400px",

                    background: "url(" + item.imgUrl + ")",
                    backgroundPosition: "center",
                    backgroundSize: "100% 100%",
                    backgroundRepeat: "no-repeat",
               
                    
                  }}
                >
                  {item.imgComponent || ""}
                </div>
                <label
                  style={{
                    color: item.color || "white",
                    fontSize: "45px",
                    fontWeight: "1200",
                  }}
                >
                  {item.title}
                </label>
              </div>
            ))}
          />
        </div>
      </div>
    </div>
  );
}
